import React, { useState } from "react";
import { useHistory } from "react-router-dom";
export default function CreateUser() {
  const [name, setName] = useState("");
  const [phone, setphone] = useState("");
  const [email, setEmail] = useState("");
  const [time, setTime] = useState(new Date().toDateString());

  const history = useHistory();

  const handleSubmit = () => {
    addUser(name, phone, email);
  };
  function addUser(name, phone, email) {
    fetch("https://60c83b2fafc88600179f660c.mockapi.io/user/users", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        name,
        phone,
        email
      })
    })
      .then((data) => data.json())
      .then((data) => {
        history.push("/users");
        alert("User added !");
      })
      .catch((e) => console.log(e));
  }
  return (
    <div className="container">
      <div className="row">
        <div className="col-lg-12">
          <label style={{ float: "left" }}>Name</label>
          <input
            type="text"
            name="name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="form-control"
            required
          />
        </div>
      </div>
      <div className="row mt-2">
        <div className="col-lg-12">
          <label style={{ float: "left" }}>Phone</label>
          <input
            type="text"
            name="phone"
            value={phone}
            onChange={(e) => setphone(e.target.value)}
            className="form-control"
            required
          />
        </div>
      </div>
      <div className="row mt-2">
        <div className="col-lg-12">
          <label style={{ float: "left" }}>Email</label>
          <input
            type="email"
            name="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="form-control"
            required
          />
        </div>
      </div>
      <div className="row mt-2">
        <div className="col-lg-12">
          <label style={{ float: "left" }}>Create At : {time}</label>
          <br />
        </div>
      </div>
      <div className="row mt-3">
        <div className="col-lg-12">
          <input
            className="btn btn-primary"
            type="submit"
            onClick={handleSubmit}
          />
        </div>
      </div>
    </div>
  );
}
